/* dropdown.css */

/* Container holding the dropdown button and list */
.dropdown-container {
  position: relative;
}

/* Dropdown button style */
.dropdown-button {
  background-color: #3498db;
  color: #ffffff;
  padding: 10px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.2s;
}

/* Dropdown list style */
.dropdown-list {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #f1f1f1;
  list-style: none;
  padding: 0;
  margin: 0;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-height: 150px;
  overflow-y: auto;
  display: none;
}

/* Individual dropdown item style */
.dropdown-list li {
  padding: 8px 12px;
  cursor: pointer;
  transition: background-color 0.2s;
}

/* Decorate all dropdown items */
.dropdown-list li:not(:last-child) {
  border-bottom: 1px solid #ccc;
}

/* Style the default "Select an option" item */
.dropdown-list li:first-child {
  font-style: italic;
  width: 100%;
  box-sizing: border-box;
}

/* Dropdown open style */
.dropdown-container.open .dropdown-list {
  display: block;
}

/* Optional: Add some animation */
.dropdown-list {
  opacity: 0;
  transform: translateY(-10px);
  transition: opacity 0.2s, transform 0.2s;
}

.dropdown-container.open .dropdown-list {
  opacity: 1;
  transform: translateY(0);
}
